import React from "react";
import "./Match.css";
import { NavLink } from "react-router-dom";
const Match = () => {
  return (
    <div className="border-components match">
      <div className="name-components"> Match </div>
      <div className="match-list">
        <NavLink to="match/1" className="navlink-match">
          <div className="match-info">
            <div className="info-team">
              <img src={process.env.PUBLIC_URL + `Img/img1.jpg`} />
              <div className="des-info">
                <div className="name-team">Team : Hng l 2 FC</div>
                <div className="des">
                  Chi tit : Anh em giao lu la chinh , th thao la 10 . Xin chi
                  giao a!
                </div>
              </div>
            </div>
            <div className="info-match">
              <img src={process.env.PUBLIC_URL + `Img/img1.jpg`} />
              <div className="des-info">
                <div className="name-team">Sn : Tn gi o</div>
                <div className="des">ia chi : gi gi gi gi gi o</div>
              </div>
            </div>
          </div>
        </NavLink>
      </div>
    </div>
  );
};

export default Match;
